@import url("https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans:ital,wght@0,300;1,300&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  margin: 0;
  box-sizing: border-box;
}

h1, h2, h3 {
  @apply font-josefin-sans uppercase text-black text-[1.75rem] lg:text-[3.125rem] leading-none;
}

body {
  @apply text-[0.938rem] text-darkGray;
}

.container {
  @apply px-[30px] lg:px-5;
}

.btn {
  @apply min-w-[200px] lg:min-w-[160px] border border-black p-4 lg:p-[9px] text-center text-black uppercase tracking-[0.5em] transition-colors;
}
.btn:hover {
  @apply bg-black text-white;
}

.header-fixed {
  @apply absolute w-full pt-[50px] lg:pt-[60px] transition-colors z-10;
}
.header-fixed .main-menu {
  @apply hidden lg:flex w-full lg:flex-initial lg:basis-0;
}
.header-fixed .main-menu-wrap {
  @apply lg:flex lg:gap-8 lg:justify-end;
}
.header-fixed .mobile-menu-hamburger .menu-close {
  @apply hidden;
}
.header-fixed .mobile-menu-hamburger .menu-close.active {
  @apply block;
}
.header-fixed .mobile-menu-hamburger .menu-open.hide {
  @apply hidden;
}
.header-fixed.active {
  @apply bg-black fixed h-screen z-10;
}
.header-fixed.active .main-menu {
  @apply flex items-center;
  height: calc(100vh - 82px);
}

.menu-item {
  @apply mb-12 lg:mb-0;
}
.menu-item a {
  @apply text-[1.5rem] text-white font-josefin-sans uppercase leading-none lg:normal-case lg:font-sans lg:text-[1.125rem] relative;
}
.menu-item a:after {
  content: "";
  @apply absolute w-6 h-[2px] bg-white left-1/2 bottom-[-8px] transition-opacity opacity-0;
  transform: translateX(-50%);
}
.menu-item a:hover::after {
  @apply opacity-100;
}
.menu-item:last-of-type {
  @apply mb-0;
}

.nav-wrap {
  @apply flex justify-between items-center flex-wrap;
}

.banner {
  @apply h-screen lg:h-auto lg:pb-[145px] lg:pt-56 relative;
}
.banner .banner-img {
  @apply absolute h-full w-full object-cover top-0 object-top;
  z-index: -1;
}
.banner .container {
  @apply h-screen lg:h-auto flex items-center;
}
.banner .title-wrap {
  @apply border-2 border-white py-8 px-[34px] w-full lg:max-w-[653px] lg:px-11 lg:py-10;
}
.banner .title-wrap h1 {
  @apply font-josefin-sans uppercase text-white leading-none text-[2.188rem] lg:text-[3.875rem];
}
@media (max-width: 374px) {
  .banner .title-wrap h1 {
    @apply text-[1.688rem];
  }
}

.two-col-image {
  @apply pt-28 pb-16 lg:pt-40;
}
.two-col-image .text-wrap {
  @apply text-center pt-16 px-2.5 lg:text-left lg:pr-8 lg:pl-24 lg:pt-24;
}
.two-col-image .text-wrap h2 {
  @apply pb-5 lg:text-[3.125rem];
}
.two-col-image .column {
  @apply lg:flex lg:items-end;
}
.two-col-image .column .col-left {
  @apply lg:flex-auto lg:shrink-0 lg:max-w-[62%];
}
.two-col-image .column .col-left img {
  @apply w-full;
}
.two-col-image .column .col-right {
  @apply lg:ml-[-15%] lg:bg-white;
}

.cards {
  @apply pt-16 pb-32 lg:pb-44;
}
.cards .title-wrap {
  @apply text-center lg:flex lg:justify-between lg:items-center lg:text-left pb-14;
}
.cards .cards-wrap {
  @apply grid lg:grid-cols-4 gap-[30px];
}
.cards .card-item {
  @apply relative lg:flex lg:min-h-[450px];
}
.cards .card-item img {
  @apply absolute w-full h-full;
  z-index: -1;
}
.cards .card-item span {
  @apply block pb-8 pt-16 px-7 lg:px-10 text-[1.813rem] font-josefin-sans text-white uppercase max-w-[215px] lg:max-w-[230px] leading-none relative lg:mt-auto;
}
.cards .card-item:before {
  @apply absolute w-full h-full z-0;
  content: "";
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.4990589986) 0%, rgba(0, 0, 0, 0) 100%);
}
@screen lg {
  .cards .card-item:before {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.4990589986) 0%, rgba(0, 0, 0, 0) 100%);
  }
}
.cards .card-item:hover span {
  @apply text-black;
}
.cards .card-item:hover::before {
  background: rgba(255, 255, 255, 0.5);
}
.cards .btn-mobile-wrap {
  @apply text-center mt-10;
}

footer {
  @apply bg-black py-16 text-center;
}
footer .column {
  @apply lg:flex lg:justify-between;
}
footer .col-left img {
  @apply mx-auto mb-11 lg:mb-8 lg:ml-0;
}
footer .footer-menu .main-menu-wrap {
  @apply lg:flex lg:gap-8;
}
footer .menu-item {
  @apply mb-7 lg:mb-0;
}
footer .menu-item a {
  @apply normal-case text-base font-sans;
}
footer .social-wrap {
  @apply flex justify-center gap-5 lg:gap-4 items-center mt-14 mb-7 lg:mt-0 lg:justify-end lg:mb-9;
}
footer .social-wrap .social-item a {
  @apply relative inline-block;
}
footer .social-wrap .social-item a:after {
  content: "";
  @apply absolute w-6 h-[2px] bg-white left-1/2 bottom-[-8px] transition-opacity opacity-0;
  transform: translateX(-50%);
}
footer .social-wrap .social-item a:hover::after {
  @apply opacity-100;
}
footer .copyright {
  @apply text-veryDarkGray;
}